import React from 'react';
import QuickSearch from './quickSearchApi';
import Search from './search';

const Home = (props) =>{
    const handleHotel =(data) =>{
        
        props.history.push(`/rest/${data}`)
    }
    return(
        <div>
        <Search rid={(data) =>{handleHotel(data)}}/>
        <QuickSearch/>
        </div>
    )
}
export default Home;