import React,{Component} from 'react';
import './search.css';
const lurl = "https://edmato.herokuapp.com/location";
const Rurl = "https://edmato.herokuapp.com/restaurant?city="
class Search extends Component{
    constructor(){
        super()

        this.state={
        location: '',
        Restaurant: ''
        }
    }
    renderCity=(data) =>{
        if(data){
            return data.map((item) =>{
                return(
                    <option value={item.city}>
                        {item.city_name} - {item.name}
                    </option>
                )
            })
        }
    }
    renderRestaurant=(data) =>{
        if(data){
            return data.map((item) =>{
                return(
                    <option value={item._id}>
                        {item.name} | {item.locality}
                    </option>
                )
            })
        }
    }
    handleCity=(event)=>{
        
fetch(`${Rurl}${event.target.value}`,{method:'GET'})
.then((res)=>res.json())
.then((data) => {
    this.setState({Restaurant:data})
}) //calling out a restaurant list after selecting a city
    }
    handleRestaurant = (event) =>{
        this.props.rid(event.target.value) //calling a details page of a particular restaurant directly using dropdown
    }
    render(){
        return(
            <div className = "main-section">
                    <h1 className = "logo">e!</h1>
                    <h2>Find the best restaurants, cafes and bars</h2>
                    <form className = "form-inline">
                        <div className = "form-group">
                            <select className = "form-control" onChange={this.handleCity}>
                                <option>----Select City---</option>
                            {this.renderCity(this.state.location)}
                            </select>
                        </div>
                        <div className = "form-group">
                            <select className = "form-control location-dropdown" onChange={this.handleRestaurant}>
                                <option>----Select Restaurant---</option>
                            {this.renderRestaurant(this.state.Restaurant)}
                            </select>
                        </div>
                        </form>
                        </div> 
        )
    }
    componentDidMount(){
        fetch(lurl,{method:'GET'})
        .then((res) => res.json())
        .then((data) => {
            this.setState({location:data}) //getting a city database
        })
    }
    
}

export default Search