import React, {Component} from 'react';
import axios from 'axios';
import {Link} from 'react-router-dom';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';

const url = "https://edmato.herokuapp.com/restaurantDetails";

class RestaurantDetails extends Component {
    constructor(props){
        super(props)

        this.state={
            rest: {
                "Cuisine":[
                    { "name": ''
                },
                    {
                        "name" : ''}
                   
                    ]
            }
        }
    }
    onBack = () => {
        let mealid = sessionStorage.getItem('type');
        this.props.history.push(`/list/${mealid}`)
    }
    render(){
       var {rest} = this.state;
        return(
            <div className="container">
                <div className="panel panel-primary">
                    <div className="panel-heading">
                        <h2>{this.state.rest.name}</h2>
                    </div>
                    <div className="panel-body">
                        <div className="row">
                            <div className="col-md-12">
                                <img className="img-responsive" src={rest.thumb} style={{height:500, width:1500}}/>
                            </div>
                            <div className="col-md-12">
                                <h3>{rest.name}</h3>
                                <h4>{rest.locality}</h4>
                                <h4>{rest.address}</h4>
                            </div>
                        </div>
                        <br/>
                    <div>
                        <Tabs>
                            <TabList>
                                <Tab>Overview</Tab>
                                <Tab>Contact</Tab>
                            </TabList>
                            <TabPanel>
                                <div>
                                    <div>About this place</div>
                                    <br/>
                                    <div>Cuisine</div>
                                    <div>{rest.Cuisine[0].name},{rest.Cuisine[1].name}</div>
                                    <div>Cost For 2 : Rs. {rest.cost}</div>
                                </div>
                            </TabPanel>
                            <TabPanel>
                            <div>
                                    <div>Contact</div>
                                    <br/>
                                    <div>Phone</div>
                                    <div>845797874</div>
                                    <div>locality: {rest.locality}</div>
                                </div>
                            </TabPanel>
                        </Tabs>
                    </div>
                    <br/><br/>
                    <div>
                        <button className="btn btn-danger btn-lg" onClick= {this.onBack}>Back</button> &nbsp;
                        <Link className="btn btn-success btn-lg" to={`/order/${this.props.match.params.id}`}>Place Order</Link>
                    </div>
                    </div>
                </div>
            </div>
        )
    }
    componentDidMount(){
        let resid=this.props.match.params.id;
        axios.get(`${url}/${resid}`)
        .then((response) => {this.setState({rest:response.data[0]})})
    }
}

export default RestaurantDetails;