 import React from 'react';
 import './QuickSearch.css';
 import {Link} from 'react-router-dom';
const QuickSearch = (props) =>{
    const listMeals = ({mealData}) => {
        if(mealData){
            return mealData.map((item) =>{
return(
    <Link to={`/list/${item.mealtype}`}>
    <div className  = "box col-lg-4 col-md-6">
        <div className  = "row">
        <div className  = "box-img col-lg-6">
            <img src= {`./images/${item.name}.jpeg`} className = "Block-img"/>
        </div>
        <div className  = "box-content col-lg-6">
        <h3 className  = "l-heading">{item.name}</h3>
        <p>Start Your day with exclusive breakfast options</p>
        </div></div>
    </div>
    </Link>
)
            })
        }
    }
    return(
        <div className ="container" >
        <h1 className  = "l-heading">Quick Searches</h1>
        <p className  = "message">Discover Restaurants by type of meal</p>
        {listMeals(props)}
 
 </div>
    )
}

export default QuickSearch;